<template>
  <Contentfield>
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
        <el-breadcrumb-item><a href="/admin/adminborrow"></a></el-breadcrumb-item>
      </el-breadcrumb>
      <el-table
        :data="tableData"
        style="width: 100%"
        :default-sort="{ prop: 'date', order: 'descending' }"
      >
        <el-table-column prop="starttime" label="" sortable width="150">
        </el-table-column>
        <el-table-column prop="returntime" label="" sortable width="150">
        </el-table-column>
        <el-table-column prop="bookid" label="ID" sortable width="90">
        </el-table-column>
        <el-table-column prop="userid" label="ID" width="90"> </el-table-column>
        <el-table-column prop="during" label="" width="100"> </el-table-column>
        <el-table-column
          prop="status"
          label=""
          width="100"
          :filters="[
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
          ]"
          :filter-method="filterTag"
          filter-placement="bottom-end"
        >
          <template slot-scope="scope">
            <el-tag
              :type="
                scope.row.status == ''
                  ? ''
                  : scope.row.status == ''
                  ? 'success'
                  : scope.row.status == ''
                  ? 'danger'
                  : scope.row.status == ''
                  ? 'warning'
                  : scope.row.status == ''
                  ? 'info'
                  : 'primary'
              "
              disable-transitions
              >{{ scope.row.status }}</el-tag
            >
          </template>
        </el-table-column>
        <!-- <el-table-column prop="status" label="" width="150">
          </el-table-column> -->
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button size="mini" @click="borrowcon(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              size="mini"
              type="warning"
              @click="returncon(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="" width="100px">
          <template slot-scope="scope">
            <el-popover
              placement="top-start"
              title=""
              width="200"
              trigger="click"
            >
              <Bookcard :isbn="isbn" :donatetime="donatetime" :userid="userid"></Bookcard>
              <el-button
                slot="reference"
                size="mini"
                type="danger"
                @click="viewbookdetail(scope.$index, scope.row)"
                ></el-button
              >
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </Contentfield>
</template>

<script>
import Bookcard from "@/components/MypageCom/MyBorrowCom/Bookcard.vue";
import Contentfield from "../../components/AdminCom/Contentfield.vue";
export default {
  components: { Contentfield, Bookcard },
  data() {
    return {
      userid: "",
      isbn: "",
      donatetime: "",
      tableData: [],
    };
  },
  mounted() {
    this.getdata();
  },
  methods: {
    getdata() {
      let vm = this;
      this.$axios({
        url: "/user/findallborrow",
        method: "post",
        data: vm.$myglobal.nowuserid,
        headers: {
          "Content-Type": "text/plain",
        },
      }).then((res) => {
        for (let item of res.data) {
          console.log(item);
          vm.tableData.push(item);
        }
      });
    },
    borrowcon(index, row) {
      var _this = this;
      if (row.status != "") {
        _this.$message({
          showClose: true,
          message: "",
          type: "warning",
        });
      } else {
        this.$axios.post("/changeborrow", {
          userid: row.userid,
          bookid: row.bookid,
          status: "",
        });
        _this.$message({
          showClose: true,
          message: "",
          type: "success",
        });
      }
    },
    returncon(index, row) {
      var _this = this;
      console.log(index, row);
      if (row.status != "") {
        _this.$message({
          showClose: true,
          message: "",
          type: "warning",
        });
      } else {
        this.$axios.post("/changeborrow", {
          userid: row.userid,
          bookid: row.bookid,
          status: "",
        });
        _this.$message({
          showClose: true,
          message: "",
          type: "success",
        });
      }
    },
    filterTag(value, row) {
      return row.status === value;
    },
    viewbookdetail(index, row) {
      let _this = this;
      this.$axios({
        url: "/user/getbookdetail",
        method: "post",
        data: row.bookid,
        headers: {
          "Content-Type": "text/plain",
        },
      }).then((res) => {
        console.log(res);
        _this.donatetime = res.data.donatetime;
        _this.isbn = res.data.isbn;
        _this.userid = res.data.userid;
      });
    },
  },
};
</script>

<style scoped>
.rwd-table {
  position: absolute;
  top: 150px;
  left: 300px;
}
.el-breadcrumb {
  height: 50px;
}
</style>



