<template>
  <div>
    <el-breadcrumb separator="/" style="margin-top: 40px; margin-left: 350px">
      <el-breadcrumb-item :to="{ path: '/user' }"></el-breadcrumb-item>
      <el-breadcrumb-item><a href="/user/myborrow"></a></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- <input type="text" name="text" class="input" placeholder="" /> -->
    <div class="rwd-table">
      <el-table
        :data="tableData"
        style="width: 100%"
        :default-sort="{ prop: 'date', order: 'descending' }"
      >
        <el-table-column prop="starttime" label="" sortable width="170">
        </el-table-column>
        <el-table-column prop="returntime" label="" sortable width="170">
        </el-table-column>
        <el-table-column prop="bookid" label="ID" sortable width="100">
        </el-table-column>
        <el-table-column prop="during" label="" width="100"> </el-table-column>
        <el-table-column
          prop="status"
          label=""
          width="100"
          :filters="[
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
          ]"
          :filter-method="filterTag"
          filter-placement="bottom-end"
        >
          <template slot-scope="scope">
            <el-tag
              :type="
                scope.row.status == ''
                  ? ''
                  : scope.row.status == ''
                  ? 'success'
                  : scope.row.status == ''
                  ? 'danger'
                  : scope.row.status == ''
                  ? 'warning'
                  : scope.row.status == ''
                  ? 'info'
                  : 'primary'
              "
              disable-transitions
              >{{ scope.row.status }}</el-tag
            >
          </template>
        </el-table-column>
        <!-- <el-table-column prop="status" label="" width="150">
        </el-table-column> -->
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button size="mini" @click="returnreq(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              size="mini"
              type="warning"
              @click="borrowcan(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="" width="100px">
          <template slot-scope="scope">
            <el-popover
              placement="top-start"
              title=""
              width="200"
              trigger="click"
            >
              <Bookcard
                :isbn="isbn"
                :donatetime="donatetime"
                :userid="userid"
              ></Bookcard>
              <el-button
                slot="reference"
                size="mini"
                type="danger"
                @click="viewbookdetail(scope.$index, scope.row)"
                ></el-button
              >
            </el-popover>
            <!--         
            <el-popover
              placement="top-start"
              title=""
              width="300"
              height="1000"
              trigger="click"
            >
              <Bookcard
                :isbn="isbn"
                :donatetime="donatetime"
                :useridd="useridd"
              ></Bookcard>

            </el-popover> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import Bookcard from "@/components/MypageCom/MyBorrowCom/Bookcard.vue";
export default {
  components: { Bookcard },
  data() {
    return {
      userid: "",
      isbn: "",
      donatetime: "",
      tableData: [],
    };
  },
  mounted() {
    this.getdata();
  },
  methods: {
    getdata() {
      let vm = this;
      this.$axios({
        url: "/user/findallborrow",
        method: "post",
        data: vm.$myglobal.nowuserid,
        headers: {
          "Content-Type": "text/plain",
        },
      }).then((res) => {
        for (let item of res.data) {
          console.log(item);
          vm.tableData.push(item);
        }
      });
    },
    returnreq(index, row) {
      let _this = this;
      if (row.status != "") {
        _this.$message({
          showClose: true,
          message: "",
          type: "warning",
        });
      } else {
        this.$axios.post("/changeborrow", {
          userid: _this.$myglobal.nowuserid,
          bookid: row.bookid,
          status: "",
        });
        _this.$message({
          showClose: true,
          message: "",
          type: "success",
        });
      }
    },
    borrowcan(index, row) {
      let _this = this;
      if (row.status != "") {
        _this.$message({
          showClose: true,
          message: "",
          type: "warning",
        });
      } else {
        this.$axios.post("/changeborrow", {
          userid: _this.$myglobal.nowuserid,
          bookid: row.bookid,
          status: "",
        });
        _this.$message({
          showClose: true,
          message: "",
          type: "success",
        });
      }
    },
    filterTag(value, row) {
      return row.status === value;
    },
    viewbookdetail(index, row) {
      let _this = this;
      this.$axios({
        url: "/user/getbookdetail",
        method: "post",
        data: row.bookid,
        headers: {
          "Content-Type": "text/plain",
        },
      }).then((res) => {
        console.log(res);
        _this.donatetime = res.data.donatetime;
        _this.isbn = res.data.isbn;
        _this.userid = res.data.userid;
      });
    },
  },
};
</script>

<style scoped>
.input {
  position: absolute;
  top: 100px;
  right: 25px;
  border: none;
  border-radius: 15px;
  padding: 15px;
  background-color: #e8e8e8;
  box-shadow: 6px 6px 12px #ffffff, -6px -6px 12px #c5c5c5;
  font-size: medium;
  font-weight: bold;
  max-width: 200px;
}

.input:focus {
  outline-color: white;
  place-content: "Enter your message!";
}
.rwd-table {
  position: absolute;
  top: 190px;
  left: 330px;
}
</style>



